export default "import React, {Component} from 'react';\nimport {Button, Modal} from \"shineDev\";\n\nclass Centered extends Component {\n  state = {isOpen: false};\n\n  onOpenModal = () => this.setState({isOpen: true});\n\n  render() {\n    return (\n      <div className=\"shine-modal-helper\">\n        <Button bgColor=\"primary\" className=\"shine-btn-display\" onClick={this.onOpenModal}>垂直居中</Button>\n        <Modal {...{\n          isOpen: this.state.isOpen,\n          title: '林语堂语录',\n          isCentered: true,\n        }}\n        >\n          <p>\n            孤独这两个字拆开来看，有孩童，有瓜果，有小犬，有蝴蝶，\n            足以撑起一个盛夏傍晚间的巷子口，人情味十足。稚儿擎瓜柳棚下，\n            细犬逐蝶窄巷中，人间繁华多笑语，惟我空余两鬓风。孩童水果猫狗飞蝶当然热闹，\n            可都和你无关，这就叫孤独。\n          </p>\n        </Modal>\n      </div>\n    );\n  }\n}\n\nexport default Centered;\n"